import React, { useState, useEffect } from 'react';
import './Camps.scss';

export default function Camps(props) {

    const [apiData, setApiData] = useState(null);

    async function getNews() {

        const fetchHeaders = new Headers();
        fetchHeaders.append('Accept', 'application/json');

        try {
            const request = await fetch('https://api.mediehuset.net/mediesuset/camps', { headers: fetchHeaders });
            const response = await request.json();
            console.log(response);
            setApiData(response);
        } catch (error) {
            console.log('getNews -> Error', error);
        }
    }

    useEffect(() => {
        getNews()
    }, [])

    return (
        <div className="Camps">
<h2>CAMPS</h2>
<p>På Mediesuset finder du tre camp områder som hver
    skiller sig ud med deres specielle stemning om du enten
    er til fest og ballade eller komfort og service på højt nivea.
    På alle camp områder er der adgang til forplejning i form af mad 
    og drikke, og toilet og bade faciliteter. Cam områderne
    er placeret tæt på festivalpladsen så at vores gæster hurtigt
    kan komme til og fra områderne i mellem de mange events.
    Det er mulgit at reservere pladser på de forskellige camp områder - dog
    krøver Camp Kultunaut og De Luxe 4 dages partout armbånd og De Luxe armbånd. 
    Reservation kan foretages når du bestiller billet.
</p>
                <div className="campGrid">
                {
                    apiData && apiData.items.map((item, i) =>
                        <div key={i}>
                            <div className="gridItem">
                            <img src={item.image} />
                                <h3>{item.name}</h3>
                                <p>{item.description.slice(0,200)}</p>
                                <p>Samlet antal pladser: {item.num_people}</p>
                                <button>LÆS MERE</button>
                            </div>
                        </div>
                    )
                }</div></div>

    )
}