import React, { useState, useEffect } from 'react';
import './Billet.scss';


export default function FetchData(props) {

    const [apiData, setApiData] = useState(null);

    async function getNews() {

        const fetchHeaders = new Headers();
        fetchHeaders.append('Accept', 'application/json');

        try {
            const request = await fetch('https://api.mediehuset.net/mediesuset/tickets', { headers: fetchHeaders });
            const response = await request.json();
            console.log(response);
            setApiData(response);
        } catch (error) {
            console.log('getNews -> Error', error);
        }
    }

    useEffect(() => {
        getNews()
    }, [])
    return (
        <div>
            <section className="billet">
                <h2>KØB BILLET</h2>
                <div className="billetgrid"><div className="gridItem">
                <h3 className="title">PARTOUT BILLET - ALLE DAGE</h3>
                {
                    apiData && apiData.items.slice(0,2).map((item, i) =>
                        <div key={i}>
                            <div className="ticketgrid">
                                <h3>{item.name}</h3>
                                <h3>{item.price} DKK</h3>
                                <button>KØB BILLET</button>
                                </div>
                            </div>
                        
                    )
                }</div>


                <div className="gridItem">
                <h3 className="title">ENKELTBILETTER</h3>
                {
                    apiData && apiData.items.slice(2,10).map((item, i) =>
                        <div key={i}>
                            <div className="ticketgrid">
                                <h3>{item.name}</h3>
                                <h3>{item.price} DKK</h3>
                                <button>KØB BILLET</button>
                            </div>
                        </div>
                    )
                }</div></div>
            </section>
        </div>
    )
}

