import Nyheder from '../Pages/Nyheder/Nyheder';
import Login from '../Pages/Login/Login';
import Camps from '../Pages/Camps/Camps';
import Program from '../Pages/Events/Program';
import Praktisk from '../Pages/Praktisk/Praktisk';
import Arkiv from '../Pages/Nyheder/Arkiv';
import Lineup from '../Pages/Events/Line-up';
import Billet from '../Pages/Billet/Billet';


/**
 * Array til at styre routes med
 * @name String Navn som bliver vist i navbar
 * @path String Sti
 * @exact Bool Grad af match
 * @display Bool Angiver om punktet skal vises i navbar
 * @component Object Komponent som skal afvikles 
 */

const routes = [
{
    name: 'NYHEDER',
    path: '/',
    exact: true,
    component: Nyheder
},
{
    name: 'EVENTS',
    path: '/program',
    exact: true,
    display:true,
    component: Program
},
{
    name: 'CAMPS',
    path: '/camps',
    exact: true,
    component: Camps
},
{
    name: 'PRAKTISK INFO',
    path: '/praktisk-info',
    exact: true,
    component: Praktisk
},
{
    name: 'LOGIN',
    path: '/login',
    exact: true,
    display: true,
    component: Login
    },
    {
        name: 'ARKIV',
        path: '/arkiv',
        exact: false,
        display: false,
        component: Arkiv
    },
    {
        name: 'LINE-UP',
        path: '/line-up',
        exact: false,
        display: false,
        component: Lineup
    },
    {
        name: 'BILLET',
        path: '/billet',
        exact: false,
        display: false,
        component: Billet
    }
];

export default routes;